import * as React from "react";

import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TablePagination from "@mui/material/TablePagination";
import TableRow from "@mui/material/TableRow";

import IconButton from "@mui/material/IconButton";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import Collapse from "@mui/material/Collapse";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";

const columns = [
  { id: "idSolicitud", label: "ID", minWidth: 80 },
  { id: "fechaSolicitud", label: "Fecha de Reserva", minWidth: 100 },
  { id: "nombreMateria", label: "Materia", minWidth: 100, align: "center" },
  {
    id: "motivoSolicitud",
    label: "Motivo Solicitud",
    minWidth: 170,
    align: "right",
  },
  {
    id: "horaInicialSolicitud",
    label: "Hora Inicial",
    minWidth: 100,
    align: "right",
  },
  {
    id: "horaFinalSolicitud",
    label: "Hora Final",
    minWidth: 100,
    align: "right",
  },
  {
    id: "capacidadSolicitud",
    label: "Capacidad",
    minWidth: 100,
    align: "right",
  },
];

function createData(
  idSolicitud,
  nombreMateria,
  motivoSolicitud,
  fechaSolicitud,
  horaInicialSolicitud,
  horaFinalSolicitud,
  capacidadSolicitud,
  nombresDocentes,
  nombresAmbientes
) {
  return {
    idSolicitud,
    nombreMateria,
    motivoSolicitud,
    fechaSolicitud,
    horaInicialSolicitud,
    horaFinalSolicitud,
    capacidadSolicitud,
    nombresDocentes,
    nombresAmbientes,
  };
}

export default function SolicitudRapidaTable({ solicitudes }) {
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);
  const [expandedRow, setExpandedRow] = React.useState(-1);

  const handleExpandClick = (index) => {
    setExpandedRow(expandedRow === index ? -1 : index);
  };

  const rows = solicitudes.map((solicitud) =>
    createData(
      solicitud.idSolicitud,
      solicitud.nombreMateria,
      solicitud.motivoSolicitud,
      solicitud.fechaSolicitud,
      solicitud.horaInicialSolicitud.split(":").slice(0, 2).join(":"),
      solicitud.horaFinalSolicitud.split(":").slice(0, 2).join(":"),
      solicitud.capacidadSolicitud,
      solicitud.nombresDocentes,
      solicitud.nombresAmbientes
    )
  );

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  return (
    <Paper sx={{ width: "100%", overflow: "hidden" }}>
      <TableContainer sx={{ maxHeight: 440 }}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
            <TableCell />
              {/* Celda de tabla vaca para el icono de expansin */}
              {columns.map((column) => (
                <TableCell
                  key={column.id}
                  align={column.align}
                  // style={{ minWidth: column.minWidth }}
                >
                  {column.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows
              .sort(
                (a, b) =>
                  new Date(a.fechaSolicitud) - new Date(b.fechaSolicitud)
              )
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map((row, index) => {
                return (
                  <React.Fragment key={index}>
                    <TableRow hover role="checkbox" tabIndex={-1} key={index}>
                      <TableCell>
                        <IconButton
                          aria-label="expand row"
                          size="small"
                          onClick={() => handleExpandClick(index)}
                        >
                          {expandedRow === index ? (
                            <KeyboardArrowUpIcon />
                          ) : (
                            <KeyboardArrowDownIcon />
                          )}
                        </IconButton>
                      </TableCell>
                      {columns.map((column) => {
                        const value = row[column.id];
                        return (
                          <TableCell key={column.id} align={column.align}>
                            {column.format && typeof value === "number"
                              ? column.format(value)
                              : value}
                          </TableCell>
                        );
                      })}
                    </TableRow>
                    <TableRow>
                      <TableCell
                        style={{ paddingBottom: 0, paddingTop: 0 }}
                        colSpan={columns.length}
                      >
                        <Collapse
                          in={expandedRow === index}
                          timeout="auto"
                          unmountOnExit
                        >
                          <Box margin={1}>
                            <Typography
                              variant="h6"
                              gutterBottom
                              component="div"
                            >
                              Docentes asignados
                            </Typography>
                            <Table size="small" aria-label="docentes">
                              <TableBody>
                                {row.nombresDocentes.map(
                                  (docente, indexDocente) => (
                                    <TableRow key={indexDocente}>
                                      <TableCell component="th" scope="row">
                                        {docente}
                                      </TableCell>
                                    </TableRow>
                                  )
                                )}
                              </TableBody>
                            </Table>
                            <Typography
                              variant="h6"
                              gutterBottom
                              component="div"
                            >
                              Ambientes reservados
                            </Typography>
                            <Table size="small" aria-label="ambientes">
                              <TableBody>
                                {row.nombresAmbientes.map(
                                  (ambiente, indexAmbiente) => (
                                    <TableRow key={indexAmbiente}>
                                      <TableCell component="th" scope="row">
                                        {ambiente}
                                      </TableCell>
                                    </TableRow>
                                  )
                                )}
                              </TableBody>
                            </Table>
                          </Box>
                        </Collapse>
                      </TableCell>
                    </TableRow>
                  </React.Fragment>
                );
              })}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        labelRowsPerPage="Filas por pgina:"
        rowsPerPageOptions={[10, 25, 100]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Paper>
  );
}
